import JsDspProcessor from "../../../../../common/web/jsDspProcessor";
import { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Phasor extends JsDspProcessor {
    static description: string = "Pure ramp wave";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "signal",
            description: "frequency",
            varLength: true
        },
        {
            isHot: true,
            type: "signal",
            description: "phase offset",
            varLength: true
        }
    ]
    static outlets: IOutletsMeta = [
        {
            type: "signal",
            description: "output",
            varLength: true
        }
    ]
    static args: IArgsMeta = [
        {
            type: "number",
            optional: true,
            description: "frequency",
            default: 440.
        },
        {
            type: "number",
            optional: true,
            description: "phase offset",
            default: 0.
        }
    ]
    static argsOffset: number = 0;
    static docs: string = "generators/docs/phasor.html";

    step: number;
    sample_rate_: number;
    phase_: number;

    process(inputs: Float32Array[][], outputs: Float32Array[][], parameters: Record<string, Float32Array>): boolean {

        let frequency = inputs[0][0];
        let phase = inputs[0][1];
        let outputStream = outputs[0][0];

        for (let i = 0; i < outputStream.length; i++) {

            let step = frequency[i] / this.sample_rate_;
            step = Math.max(Math.min(step, 1.), -1.);

            let phase_offset = Math.min(Math.max(phase[i], 0), 1);

            this.phase_ += step;

            if (this.phase_ >= 1)
                this.phase_ -= 1;

            else if (this.phase_ < 0.)
                this.phase_ += 1;

            outputStream[i] = (this.phase_ + phase_offset) % 1.;

        }

        return true;
    }

    init(sampleRate: number) {
        this.sample_rate_ = sampleRate;
        this.phase_ = 0;
    }
}
