export enum ADSR_SEG {
    IDLE = 0,
    ATTACK = 1,
    DECAY = 2,
    RELEASE = 4
}

export class Adsr {
    private sus_level_: number;
    private x_: number;
    private attackShape_: number;
    private attackTarget_: number;
    private attackTime_: number;
    private decayTime_: number;
    private releaseTime_: number;
    private attackD0_: number;
    private decayD0_: number;
    private releaseD0_: number;
    private sample_rate_: number;
    private mode_: ADSR_SEG;
    private gate_: boolean;

    constructor(sample_rate: number, blockSize: number = 1) {
        this.sample_rate_ = sample_rate / blockSize;
        this.attackShape_ = -1.0;
        this.attackTarget_ = 0.0;
        this.attackTime_ = -1.0;
        this.decayTime_ = -1.0;
        this.releaseTime_ = -1.0;
        this.sus_level_ = 0.7;
        this.x_ = 0.0;
        this.gate_ = false;
        this.mode_ = ADSR_SEG.IDLE;

        this.SetTime(ADSR_SEG.ATTACK, 0.1);
        this.SetTime(ADSR_SEG.DECAY, 0.1);
        this.SetTime(ADSR_SEG.RELEASE, 0.1);
    }

    public Retrigger(hard: boolean): void {
        this.mode_ = ADSR_SEG.ATTACK;
        if (hard)
            this.x_ = 0.0;
    }

    public SetTime(seg: ADSR_SEG, time: number): void {
        switch (seg) {
            case ADSR_SEG.ATTACK:
                this.SetAttackTime(time, 0.0);
                break;
            case ADSR_SEG.DECAY:
                [this.decayTime_, this.decayD0_] = this.SetTimeConstant(time, this.decayTime_, this.decayD0_);
                break;
            case ADSR_SEG.RELEASE:
                [this.releaseTime_, this.releaseD0_] = this.SetTimeConstant(time, this.releaseTime_, this.releaseD0_);
                break;
            default: return;
        }
    }

    public SetAttackTime(timeInS: number, shape: number): void {
        if ((timeInS != this.attackTime_) || (shape != this.attackShape_)) {
            this.attackTime_ = timeInS;
            this.attackShape_ = shape;
            if (timeInS > 0.0) {
                let x = shape;
                let target = 9.0 * Math.pow(x, 10.0) + 0.3 * x + 1.01;
                this.attackTarget_ = target;
                let logTarget = Math.log(1.0 - (1.0 / target)); // -1 for decay
                this.attackD0_ = 1.0 - Math.exp(logTarget / (timeInS * this.sample_rate_));
            }
            else
                this.attackD0_ = 1.0; // instant change
        }
    }

    public SetDecayTime(timeInS: number): void {
        [this.decayTime_, this.decayD0_] = this.SetTimeConstant(timeInS, this.decayTime_, this.decayD0_);
    }

    public SetReleaseTime(timeInS: number): void {
        [this.releaseTime_, this.releaseD0_] = this.SetTimeConstant(timeInS, this.releaseTime_, this.releaseD0_);
    }

    private SetTimeConstant(timeInS: number, time: number, coeff: number) {
        if (timeInS != time) {
            time = timeInS;
            if (time > 0.0) {
                const target = Math.log(1.0 / Math.E);
                coeff = 1.0 - Math.exp(target / (time * this.sample_rate_));
            }
            else
                coeff = 1.0; // instant change
        }

        return [time, coeff];
    }

    public SetSustainLevel(sus_level: number): void {
        sus_level = (sus_level <= 0.0) ? -0.01 // forces envelope into idle
            : (sus_level > 1.0) ? 1.0 : sus_level;
        this.sus_level_ = sus_level;
    }

    public GetCurrentSegment(): ADSR_SEG {
        return this.mode_;
    }

    public IsRunning(): boolean {
        return this.mode_ != ADSR_SEG.IDLE;
    }

    public Process(gate: boolean): number {
        let out = 0.0;

        if (gate && !this.gate_) // rising edge
            this.mode_ = ADSR_SEG.ATTACK;
        else if (!gate && this.gate_) // falling edge
            this.mode_ = ADSR_SEG.RELEASE;
        this.gate_ = gate;

        let D0 = this.attackD0_;
        if (this.mode_ == ADSR_SEG.DECAY)
            D0 = this.decayD0_;
        else if (this.mode_ == ADSR_SEG.RELEASE)
            D0 = this.releaseD0_;

        let target = this.mode_ == ADSR_SEG.DECAY ? this.sus_level_ : -0.01;
        switch (this.mode_) {
            case ADSR_SEG.IDLE:
                out = 0.0;
                break;
            case ADSR_SEG.ATTACK:
                this.x_ += D0 * (this.attackTarget_ - this.x_);
                out = this.x_;
                if (out > 1.0) {
                    this.x_ = out = 1.0;
                    this.mode_ = ADSR_SEG.DECAY;
                }
                break;
            case ADSR_SEG.DECAY:
            case ADSR_SEG.RELEASE:
                this.x_ += D0 * (target - this.x_);
                out = this.x_;
                if (out < 0.0) {
                    this.x_ = out = 0.0;
                    this.mode_ = ADSR_SEG.IDLE;
                }
            default: break;
        }
        return out;
    }
}
