import JsDspProcessor, { JsParamDescriptor } from "../../../../../common/web/jsDspProcessor";
import { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";


enum Mode {
    MODE_SAMPLE_HOLD,
    MODE_TRACK_HOLD,
    MODE_LAST,
}

export default class SampleAndHoldObject extends JsDspProcessor {
    static description: string = "Sample and Hold";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "signal",
            description: "Signal to be sampled/tracked and held",
        },
        {
            isHot: true,
            type: "signal",
            description: "Trigger input",
        }
    ];
    static outlets: IOutletsMeta = [
        {
            type: "signal",
            description: "Sample and hold output",
        }
    ];
    // static args: IArgsMeta = [
    //     {
    //         type: "enum",
    //         description: "Mode",
    //         enums: [
    //             "Sample Hold",
    //             "Track Hold"
    //         ],
    //         optional: true,
    //         default: "Sample Hold"
    //     }
    // ];
    static docs: string = "generators/docs/sah.html";
    static argsOffset: number = 0;

    private track_: number = 0;
    private sample_: number = 0;
    private previous_: boolean = false;

    process(inputs: Float32Array[][], outputs: Float32Array[][], parameters: Record<string, Float32Array>): boolean {
        const signalInput = inputs[0][0];
        const triggerInput = inputs[0][1];
        const outputStream = outputs[0][0];
        // const mode = parameters['mode'][0] == "Sample Hold" ? Mode.MODE_SAMPLE_HOLD : Mode.MODE_TRACK_HOLD;

        for (let i = 0; i < outputStream.length; i++) {
            this.update(triggerInput[i] > 0, signalInput[i]);
            // outputStream[i] = mode === Mode.MODE_SAMPLE_HOLD ? this.sample_ : this.track_;
            outputStream[i] = this.sample_;
        }

        return true;
    }

    private update(trigger: boolean, input: number) {
        if (trigger) {
            if (!this.previous_) {
                this.sample_ = input;
            }
            this.track_ = input;
        }
        this.previous_ = trigger;
    }

    init(sampleRate: number) {
        this.track_ = 0;
        this.sample_ = 0;
        this.previous_ = false;
    }
}
