import { Adsr, ADSR_SEG } from './adsr';
import JsDspProcessor from "../../../../../common/web/jsDspProcessor";
import { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class AdsrObject extends JsDspProcessor {
    static description: string = "ADSR Envelope";
    static docs: string = "generators/docs/adsrObject.html";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "signal",
            description: "gate input",
        },
        {
            isHot: true,
            type: "signal",
            description: "attack time in milliseconds",
        },
        {
            isHot: true,
            type: "signal",
            description: "decay time in milliseconds",
        },
        {
            isHot: true,
            type: "signal",
            description: "sustain level",
        },
        {
            isHot: true,
            type: "signal",
            description: "release time in milliseconds",
        }
    ]
    static outlets: IOutletsMeta = [
        {
            type: "signal",
            description: "envelope output",
            varLength: true
        }
    ]
    static args: IArgsMeta = [
        {
            type: "number",
            optional: true,
            description: "attack time in milliseconds",
            default: 250
        },
        {
            type: "number",
            optional: true,
            description: "decay time in milliseconds",
            default: 500
        },
        {
            type: "number",
            optional: true,
            description: "sustain level",
            default: 1
        },
        {
            type: "number",
            optional: true,
            description: "release time in milliseconds",
            default: 500
        }
    ]
    static argsOffset: number = 1;

    adsr: Adsr;

    process(inputs: Float32Array[][], outputs: Float32Array[][], parameters: Record<string, Float32Array>): boolean {

        const gate = inputs[0][0];
        const attackTime = inputs[0][1];
        const decayTime = inputs[0][2];
        const sustainLevel = inputs[0][3];
        const releaseTime = inputs[0][4];
        const outputStream = outputs[0][0];

        for (let i = 0; i < outputStream.length; i++) {
            this.adsr.SetTime(ADSR_SEG.ATTACK, attackTime[i] / 1000);
            this.adsr.SetTime(ADSR_SEG.DECAY, decayTime[i] / 1000);
            this.adsr.SetTime(ADSR_SEG.RELEASE, releaseTime[i] / 1000);
            this.adsr.SetSustainLevel(sustainLevel[i]);
            outputStream[i] = this.adsr.Process(gate[i] > 0);
        }

        return true;
    }

    init(sampleRate: number) {
        this.adsr = new Adsr(sampleRate);
    }
}
