import JsDspProcessor from "../../../../../common/web/jsDspProcessor";
import { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";
import { Oscillator, Waveform } from "./oscillator";

export default class Tri extends JsDspProcessor {
    static description: string = "Triangle wave oscillator"
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "signal",
            description: "frequency",
            varLength: true
        }
    ]
    static outlets: IOutletsMeta = [
        {
            type: "signal",
            description: "output",
            varLength: true
        }
    ]
    static args: IArgsMeta = [
        {
            type: "number",
            optional: true,
            description: "frequency",
            default: 440.
        }
    ]
    static argsOffset: number = 0;
    static docs: string = "generators/docs/tri.html";

    osc: Oscillator;

    process(inputs: Float32Array[][], outputs: Float32Array[][], parameters: Record<string, Float32Array>): boolean {

        let frequency = inputs[0][0];
        let outputStream = outputs[0][0];

        for (let i = 0; i < outputStream.length; i++) {
            this.osc.SetFreq(frequency[i]);
            outputStream[i] = this.osc.Process();
        }

        return true;
    }

    init(sampleRate: number) {
        this.osc = new Oscillator(sampleRate);
        this.osc.SetAmp(1);
        this.osc.SetWaveform(Waveform.WAVE_POLYBLEP_TRI);
    }
}
