export enum Waveform {
    WAVE_SIN,
    WAVE_TRI,
    WAVE_SAW,
    WAVE_RAMP,
    WAVE_SQUARE,
    WAVE_POLYBLEP_TRI,
    WAVE_POLYBLEP_SAW,
    WAVE_POLYBLEP_SQUARE,
    WAVE_LAST,
}

export class Oscillator {
    private waveform: Waveform;
    private amp: number;
    private freq: number;
    private pw: number;
    private srRecip: number;
    private phase: number;
    private phaseInc: number;
    private lastOut: number;
    private eor: boolean;
    private eoc: boolean;
    private nyquist: number;

    constructor(sampleRate: number) {
        this.srRecip = 1.0 / sampleRate;
        this.nyquist = sampleRate / 2.0;
        this.freq = 100.0;
        this.amp = 0.5;
        this.pw = 0.5;
        this.phase = 0.0;
        this.phaseInc = this.CalcPhaseInc(this.freq);
        this.waveform = Waveform.WAVE_SIN;
        this.eoc = true;
        this.eor = true;
        this.lastOut = 0.0;
    }

    public SetFreq(f: number): void {
        const clamped = this.fclamp(f, 0.0, this.nyquist);
        this.freq = clamped;
        this.phaseInc = this.CalcPhaseInc(clamped);
    }

    public SetAmp(a: number): void {
        this.amp = a;
    }

    public SetWaveform(wf: Waveform): void {
        this.waveform = wf < Waveform.WAVE_LAST ? wf : Waveform.WAVE_SIN;
    }

    public SetPw(pw: number): void {
        this.pw = this.fclamp(pw, 0.0, 1.0);
    }

    public IsEOR(): boolean {
        return this.eor;
    }

    public IsEOC(): boolean {
        return this.eoc;
    }

    public IsRising(): boolean {
        return this.phase < 0.5;
    }

    public IsFalling(): boolean {
        return this.phase >= 0.5;
    }

    public PhaseAdd(_phase: number): void {
        this.phase += _phase;
    }

    public Reset(_phase: number = 0.0): void {
        this.phase = _phase;
    }

    private CalcPhaseInc(f: number): number {
        return f * this.srRecip;
    }

    private fclamp(value: number, min: number, max: number): number {
        return Math.min(Math.max(value, min), max);
    }

    public Process(): number {
        let out: number;
        let t: number;
        switch (this.waveform) {
            case Waveform.WAVE_SIN:
                out = Math.sin(this.phase * 2 * Math.PI);
                break;
            case Waveform.WAVE_TRI:
                t = -1.0 + (2.0 * this.phase);
                out = 2.0 * (Math.abs(t) - 0.5);
                break;
            case Waveform.WAVE_SAW:
                out = -1.0 * ((this.phase * 2.0) - 1.0);
                break;
            case Waveform.WAVE_RAMP:
                out = (this.phase * 2.0) - 1.0;
                break;
            case Waveform.WAVE_SQUARE:
                out = this.phase < this.pw ? 1.0 : -1.0;
                break;
            case Waveform.WAVE_POLYBLEP_TRI:
                t = this.phase;
                out = this.phase < 0.5 ? 1.0 : -1.0;
                out += this.Polyblep(this.phaseInc, t);
                out -= this.Polyblep(this.phaseInc, (t + 0.5) % 1.0);
                // Leaky Integrator: y[n] = A + x[n] + (1 - A) * y[n-1]
                out = this.phaseInc * out + (1.0 - this.phaseInc) * this.lastOut;
                this.lastOut = out;
                out *= 4.0; // normalize amplitude after leaky integration
                break;
            case Waveform.WAVE_POLYBLEP_SAW:
                t = this.phase;
                out = (2.0 * t) - 1.0;
                out -= this.Polyblep(this.phaseInc, t);
                out *= -1.0;
                break;
            case Waveform.WAVE_POLYBLEP_SQUARE:
                t = this.phase;
                out = this.phase < this.pw ? 1.0 : -1.0;
                out += this.Polyblep(this.phaseInc, t);
                out -= this.Polyblep(this.phaseInc, ((t + (1.0 - this.pw)) % 1.0));
                out *= 0.707; // ?
                break;
            default:
                out = 0.0;
                break;
        }
        this.phase += this.phaseInc;
        if (this.phase > 1.0) {
            this.phase -= 1.0;
            this.eoc = true;
        } else {
            this.eoc = false;
        }
        this.eor = (this.phase - this.phaseInc < 0.5 && this.phase >= 0.5);
        return out * this.amp;
    }

    private Polyblep(phaseInc: number, t: number): number {
        let dt = phaseInc;
        if (t < dt) {
            t /= dt;
            return t + t - t * t - 1.0;
        } else if (t > 1.0 - dt) {
            t = (t - 1.0) / dt;
            return t * t + t + t + 1.0;
        } else {
            return 0.0;
        }
    }

}
